/* eslint-disable react/prop-types */
import { useEffect, useState } from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

const DragDropRenderer = props => {
    const { id, column } = props;

    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging,
        isSorting
    } = useSortable({
        id
    });

    // this will probably not work with resizable columns
    const [lineage, setLineage] = useState();

    const dragStyle = {
        cursor: 'pointer',
        transform: CSS.Transform.toString(transform),
        transition
    };

    // Basically this whole lineage thing is a hack until @dnd-kit adds `data` to the options you can pass to onDragEnd()
    useEffect(() => {
        if (!column) return;

        const arr = [];
        let current = column;
        while (current.parent) {
            arr.push(current.parent.id);
            current = current.parent;
        }
        setLineage(arr);
    }, [column]);

    return {
        'data-lineage': lineage,
        setNodeRef,
        dragStyle,
        ...attributes,
        ...listeners,
        transform,
        transition,
        isDragging,
        isSorting
    };
};

export default DragDropRenderer;
