/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TableBodyDefaultProps,
    TableBodyPropTypes
} from './generated/propTypes';

const TableBody = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        children,
        className,
        style,
        width,
        ...customProps
    } = props;

    const styles = { ...style };

    if (width) styles.width = width;

    const Tag = alwaysUseDivTags ? 'div' : 'tbody';

    return (
        <Tag
            ref={externalRef}
            className={clsx(
                'mdc-data-table__content lmnt-table__body',
                className
            )}
            {...customProps}
            style={styles}
        >
            {children}
        </Tag>
    );
});

TableBody.displayName = 'TableBody';
TableBody.propTypes = TableBodyPropTypes;
TableBody.defaultProps = TableBodyDefaultProps;

export default TableBody;
