import React from 'react';
import { edsRandomId } from '@element/react-common';
import sortBy from 'lodash/sortBy';
import { Textfield } from '@element/react-textfield';
import { Checkbox } from '@element/react-checkbox';
import { Switch } from '@element/react-switch';
import { Datepicker } from '@element/react-datepicker';

export const scrollbarWidthCalculator = () => {
    // thanks too https://davidwalsh.name/detect-scrollbar-width
    const scrollDiv = document.createElement('div');
    scrollDiv.setAttribute(
        'style',
        'width: 100px; height: 100px; overflow: scroll; position:absolute; top:-9999px;'
    );
    document.body.appendChild(scrollDiv);
    const finalScrollbarWidth = scrollDiv.offsetWidth - scrollDiv.clientWidth;
    document.body.removeChild(scrollDiv);
    return finalScrollbarWidth;
};

const formatIntrinsicValue = value => {
    if (!value && value !== 0) return null;
    if (typeof value === 'object') {
        if (value instanceof Date) return value.toLocaleDateString();
        if (React.isValidElement(value)) return value;
        // make a reasonable attempt at getting a display value by trying some common props. We could use the `value?.label || value?.text` format, but lets be formal about it for now.
        if (Object.prototype.hasOwnProperty.call(value, 'label'))
            return value.label;
        if (Object.prototype.hasOwnProperty.call(value, 'text'))
            return value.text;
        if (Object.prototype.hasOwnProperty.call(value, 'display'))
            return value.display;
        if (Object.prototype.hasOwnProperty.call(value, 'displayValue'))
            return value.displayValue;
        if (Object.prototype.hasOwnProperty.call(value, 'displayLabel'))
            return value.displayLabel;
        if (Object.prototype.hasOwnProperty.call(value, 'value'))
            return value.value;
        // this will look bad, but we don't have much choice
    }
    return String(value);
};

const formatCurrency = (
    value,
    { country = 'en-US', currency = 'USD', maximumSignificantDigits }
) =>
    new Intl.NumberFormat(country, {
        style: 'currency',
        currency,
        maximumSignificantDigits
    }).format(value);

const formatCellValue = props => {
    const {
        accessor,
        row,
        onCellUpdate,
        cell,
        cell: {
            value,
            column: {
                cellTemplate,
                displayConfig,
                displayTemplate,
                displayType,
                editable
            }
        }
    } = props;

    // if they want to use the react-table syntax

    const { cells, id: rowId } = row;

    // automatically override display type when a template is passed in
    const displayTypeActual =
        cellTemplate || displayTemplate ? 'custom' : displayType;

    // if (value === null || value === undefined) return '';

    if (!displayTypeActual) return formatIntrinsicValue(value);
    if (displayTypeActual.indexOf('currency') > -1)
        return formatCurrency(value, displayTypeActual);

    switch (displayTypeActual) {
        case 'text':
            return String(value);
        case 'percentage':
            return `${value}%`;
        case 'date':
            return new Date(value)?.toLocaleDateString?.();
        case 'time':
            return new Date(value)?.toLocaleTimeString?.();
        case 'datetime':
            return new Date(value)?.toLocaleString?.();
        case 'currency':
            return formatCurrency(value, displayConfig);
        case 'checkbox':
            return (
                <Checkbox
                    dense
                    checked={value}
                    onChange={onCellUpdate(accessor, rowId)}
                />
            );
        case 'switch':
            return (
                <Switch
                    dense
                    checked={value}
                    onChange={onCellUpdate(accessor, rowId)}
                />
            );
        case 'custom':
            if (cellTemplate)
                return cellTemplate({
                    cell,
                    editable,
                    onCellUpdate,
                    row,
                    value
                });

            // TODO: deprecated, remove in v6!
            if (displayTemplate)
                return displayTemplate(
                    value,
                    cells?.[0]?.row?.original,
                    cells?.[0]?.row?.id,
                    editable,
                    onCellUpdate,
                    row
                );

            return value;
        default:
            return value;
    }
};

const formatCellEditor = props => {
    const {
        accessor,
        onCellUpdate,
        row,
        onControlBlur,
        setEditedValue,
        editedValue,
        cell: {
            value,
            column: { editType, editConfig, editTemplate }
        }
    } = props;

    const valueActual = editedValue || value;
    const rowData = row.cells;
    const variant = editConfig?.variant || 'outlined';
    const handleUpdate = onCellUpdate?.(accessor, row, setEditedValue);
    const sharedProps = {
        dense: true,
        onChange: handleUpdate,
        onBlur: onControlBlur
    };

    switch (editType) {
        case 'checkbox':
            return <Checkbox checked={valueActual} {...sharedProps} />;
        case 'switch':
            return <Switch checked={valueActual} {...sharedProps} />;
        case 'datepicker':
            return (
                <Datepicker
                    variant={variant}
                    value={valueActual}
                    {...sharedProps}
                />
            );
        case 'number':
            return (
                <Textfield
                    variant={variant}
                    value={valueActual}
                    type='number'
                    {...sharedProps}
                />
            );
        case 'custom':
            return editTemplate?.(
                valueActual,
                rowData?.[0]?.row?.original,
                rowData?.[0]?.row?.id,
                onCellUpdate,
                onControlBlur
            );
        case 'text':
        case 'textfield':
        default:
            return (
                <Textfield
                    variant={variant}
                    value={valueActual}
                    {...sharedProps}
                />
            );
    }
};

const formatEditor = (accessor, onCellUpdate) => props =>
    !props.cell.column.editable
        ? formatCellValue(props)
        : formatCellEditor({ ...props, ...accessor, ...onCellUpdate });

const formatCell = (accessor, onCellUpdate) => props =>
    formatCellValue({ ...props, ...onCellUpdate, accessor });

export const formatColumns = (columns, onCellUpdate) => {
    if (!columns) return [];
    return columns.map(column => {
        const { headerTemplate } = column;
        const temp = {
            ...column,
            Cell: formatCell(column.accessor, onCellUpdate),
            Editor: formatEditor(column.accessor, onCellUpdate),
            // Make sure we always have an id, if the user didn't pass one in
            id: column.id || edsRandomId()
        };
        if (headerTemplate) temp.Header = headerTemplate;
        // recursion to catch grouped columns
        if (column.columns)
            temp.columns = formatColumns(column.columns, onCellUpdate);
        return temp;
    });
};

export const getBackgroundColor = column => {
    if (!column) return null;
    let cursor = column;
    let bgColor = '';

    while (cursor) {
        if (cursor.backgroundColor) {
            bgColor = cursor.backgroundColor;
            break;
        }
        cursor = cursor.parent;
    }
    return bgColor;
};

const callProps = (propsToCall, args) =>
    typeof propsToCall === 'function' ? propsToCall(...args) : propsToCall;

export const createProps = (globalProps, localProps, args) => ({
    ...callProps(globalProps, args),
    ...callProps(localProps, args)
});

export const getCombinedProps = (props, customProps, getTableProps) => {
    const combinedStyles = { ...props.style };
    if (props.height && !props.virtualized)
        combinedStyles.height = `${props.height}px`;

    return getTableProps?.({
        ariaLabel: props.ariaLabel,
        className: props.className,
        columnStriped: props.columnStriped,
        containerProps: props.containerProps,
        dense: props.dense,
        fixedHeader: props.fixedHeader,
        fullWidth: props.fullWidth,
        headerDividerTall: props.headerDividerTall,
        headerDividerThemeColor: props.headerDividerThemeColor,
        headerThemeColor: props.headerThemeColor,
        layout: props.layout,
        leadingContent: props.topBar || props.leadingContent,
        nested: props.nested,
        noContentIncludeTable: props.noContentIncludeTable,
        noContentMessage: props.noContentMessage,
        noHover: props.noHover,
        rowStriped: props.rowStriped,
        style: combinedStyles,
        virtualized: props.virtualized,
        width: props.width,
        ...customProps
    });
};

export const getInitialState = columns => {
    const sorting = sortBy(
        columns.reduce((out, c, i) => {
            if (c.defaultSort) {
                out.push({
                    id: c.id,
                    desc:
                        c.defaultSort === 'desc' ||
                        c.defaultSort === 'descending',
                    order: c.defaultSortOrder || i
                });
            }
            return out;
        }, []),
        'order'
    );
    return {
        sortBy: sorting
    };
};

export const getColumnAlignment = column => {
    switch (column?.align) {
        case 'left':
            return { justifyContent: 'flex-start' };
        case 'right':
            return { justifyContent: 'flex-end' };
        case 'center':
            return { justifyContent: 'center' };
        default:
            return null;
    }
};

export const getContextRootProp = (context, prop, defaultValue) => {
    if (!context || !context.rootProps) return undefined;
    const val = context.rootProps[prop];
    return val !== undefined ? val : defaultValue;
};
