/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, {
    forwardRef,
    useEffect,
    useImperativeHandle,
    useMemo,
    useRef
} from 'react';
import { MDCDataTable } from '@material/data-table';
import { edsWarning } from '@element/shared';
import TableNoContent from './TableNoContent';

const TableBase = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        fixedColumns,
        ariaLabel,
        children,
        className,
        columnStriped,
        cellProps,
        containerProps,
        defaultColumn,
        dense,
        fixedHeader,
        fullWidth,
        headerCellProps,
        headerDividerTall,
        headerDividerThemeColor,
        headerRowProps,
        headerThemeColor,
        // height,
        hideHorizontalScrollbar,
        layout,
        leadingContent,
        nested,
        noContentIncludeTable,
        noContentMessage,
        noHover,
        noInit,
        rowProps,
        rowStriped,
        style,
        trailingContent,
        virtualized,
        width,
        ...customProps
    } = props || {};

    const mdcDataTable = useRef();
    const tableRef = useRef();
    const hasContent = !!children;

    useImperativeHandle(externalRef, () => tableRef.current, []);

    useEffect(() => {
        if (
            hasContent &&
            !noInit &&
            !virtualized &&
            !alwaysUseDivTags &&
            !mdcDataTable.current
        ) {
            try {
                mdcDataTable.current = MDCDataTable.attachTo(tableRef.current);
            } catch (e) {
                edsWarning(
                    `Error rendering table: "${e.message}". This most likely means that the table contents were empty`
                );
            }
        }

        return () => {
            if (mdcDataTable.current) {
                mdcDataTable.current.destroy();
            }
        };
        // This is to prevent it from making noContent a dependency, which will cause an infinite loop. Eslint isn't always smart.
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [mdcDataTable, tableRef, virtualized, alwaysUseDivTags]);

    const classes = clsx(
        'lmnt',
        'mdc-data-table',
        'lmnt-table',
        virtualized && 'lmnt-table--virtualized',
        rowStriped && 'lmnt-table__table--row-striped',
        columnStriped && 'lmnt-table__table--column-striped',
        fixedHeader && 'mdc-data-table--sticky-header',
        dense && 'lmnt-table--dense',
        noHover && 'lmnt-table--no-hover',
        headerThemeColor === 'neutral' && 'lmnt-table__header-theme--neutral',
        headerThemeColor === 'primary' && 'lmnt-table__header-theme--primary',
        headerThemeColor === 'secondary' &&
            'lmnt-table__header-theme--secondary',
        headerThemeColor === 'primary-variant' &&
            'lmnt-table__header-theme--primary-variant',
        headerThemeColor === 'secondary-variant' &&
            'lmnt-table__header-theme--secondary-variant',
        !headerThemeColor &&
            headerDividerThemeColor === 'primary' &&
            'lmnt-table__header-divider--primary',
        !headerThemeColor &&
            headerDividerThemeColor === 'secondary' &&
            'lmnt-table__header-divider--secondary',
        !headerThemeColor &&
            headerDividerThemeColor === 'primary-variant' &&
            'lmnt-table__header-divider--primary-variant',
        !headerThemeColor &&
            headerDividerThemeColor === 'secondary-variant' &&
            'lmnt-table__header-divider--secondary-variant',
        headerDividerTall && 'lmnt-table__header-divider--tall',
        nested && 'lmnt-table--nested',
        !width && fullWidth && 'lmnt-table--full-width',
        width && 'lmnt-table--fixed-width',
        hideHorizontalScrollbar && 'lmnt-table__hide-horizontal-scrollbar',
        className
    );

    const styles = { ...style };
    if (width) styles.width = `${width}px`;
    // BUG: does not work as intended
    // if (height) styles.height = `${height}px`;

    const content = useMemo(
        () =>
            hasContent ? (
                children
            ) : (
                <TableNoContent>{noContentMessage}</TableNoContent>
            ),
        [children, noContentMessage, hasContent]
    );

    const TableTag = alwaysUseDivTags ? 'div' : 'table';

    return (
        <div className={classes} ref={tableRef} style={styles} {...customProps}>
            {leadingContent}
            <div
                className={clsx(
                    'mdc-data-table__table-container',
                    'lmnt-data-table__container'
                    // (fullWidth || layout === 'flex') &&
                    //     'lmnt-data-table--flex-overflow'
                )}
                {...containerProps}
            >
                <TableTag
                    className={clsx(
                        'mdc-data-table__table',
                        'lmnt-table__table',
                        fixedHeader &&
                            layout === 'block' &&
                            'lmnt-table__block-fixed-header',
                        fixedHeader &&
                            layout === 'flex' &&
                            'lmnt-table__flex-fixed-header'
                    )}
                    aria-label={ariaLabel}
                >
                    {content}
                </TableTag>
            </div>
            {trailingContent}
        </div>
    );
});

TableBase.displayName = 'TableBase';

export default TableBase;
