/* eslint-disable react/prop-types */
import React, { useMemo } from 'react';
import ColumnDndSorter from './ColumnDragDropRenderer';

const SpecialContextContainer = props => {
    const { columnSorting, children } = props;

    const ColumnSortContext = useMemo(
        () =>
            // eslint-disable-next-line react/no-unstable-nested-components
            ({ headerGroup, onColumnSort, columnReordering }) =>
                columnReordering ? (
                    <ColumnDndSorter
                        onUpdate={onColumnSort}
                        itemIds={headerGroup.headers.map(h => h.id)}
                    >
                        {children}
                    </ColumnDndSorter>
                ) : (
                    children
                ),
        [children]
    );
    return columnSorting && <ColumnSortContext {...columnSorting} />;
};

export default SpecialContextContainer;
