/* eslint-disable react/prop-types */
import React from 'react';
import TableBody from '../TableBody';
import TableCell from '../TableCell';
import TableNoContent from '../TableNoContent';
import TableRow from '../TableRow';
import renderRows from './renderRows';
import TableRowVirtualRenderer from './TableRowVirtualRenderer';
import TableRowRenderer from './TableRowRenderer';

const defaultHeight = 500;

export default ({ alwaysUseDivTags, tableProps, rtProps, hasData, rows }) => {
    const {
        cellProps,
        columns,
        expandable,
        expandedRowTemplate,
        height,
        initiallyExpanded,
        layout,
        noContentIncludeTable,
        noContentMessage,
        rowHeight,
        rowProps,
        variableHeight,
        virtualized,
        width
    } = tableProps;
    const { getTableBodyProps, prepareRow, totalWidth, visibleColumns } =
        rtProps;

    const showNoContent = !hasData && noContentIncludeTable;

    const VirtualList = virtualized
        ? TableRowVirtualRenderer
        : TableRowRenderer;

    return (
        <TableBody
            alwaysUseDivTags={alwaysUseDivTags}
            {...getTableBodyProps?.()}
        >
            {showNoContent && (
                <TableRow>
                    <TableCell colSpan={columns?.length}>
                        <TableNoContent>{noContentMessage}</TableNoContent>
                    </TableCell>
                </TableRow>
            )}
            <VirtualList
                rowHeight={rowHeight}
                height={height || defaultHeight}
                rowCount={rows?.length || 0}
                totalWidth={totalWidth}
                variableHeight={variableHeight}
                virtualized={virtualized}
                width={width}
            >
                {renderRows({
                    alwaysUseDivTags,
                    cellProps,
                    expandable,
                    expandedRowTemplate,
                    initiallyExpanded,
                    layout,
                    prepareRow,
                    rowProps,
                    rows,
                    visibleColumns
                })}
            </VirtualList>
        </TableBody>
    );
};
