/* eslint-disable react/prop-types */
import React from 'react';
import { FixedSizeList, VariableSizeList } from 'react-window';

const TableRowVirtualRenderer = ({
    children,
    height,
    rowHeight,
    rowCount,
    totalWidth,
    variableHeight,
    width
}) => {
    const VirtualList = variableHeight ? VariableSizeList : FixedSizeList;
    return (
        <VirtualList
            height={height}
            itemCount={rowCount}
            itemSize={rowHeight}
            width={width || totalWidth}
        >
            {children}
        </VirtualList>
    );
};

export default TableRowVirtualRenderer;
