/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TableHeaderRowDefaultProps,
    TableHeaderRowPropTypes
} from './generated/propTypes';

const TableHeaderRow = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        children,
        className,
        headerGroup, // headerGroup is here to shut up warnings, we need to figure out why it's being passed in
        ...customProps
    } = props;
    const Tag = alwaysUseDivTags ? 'div' : 'tr';

    return (
        <Tag
            ref={externalRef}
            className={clsx(
                'lmnt',
                'mdc-data-table__header-row lmnt-table__header-row',
                className
            )}
            {...customProps}
        >
            {children}
        </Tag>
    );
});

TableHeaderRow.displayName = 'TableHeaderRow';
TableHeaderRow.propTypes = TableHeaderRowPropTypes;
TableHeaderRow.defaultProps = TableHeaderRowDefaultProps;

export default TableHeaderRow;
