/* eslint-disable react/prop-types */
import React from 'react';
import TableHeader from '../TableHeader';
import TableHeaderRow from '../TableHeaderRow';
import TableHeaderCell from '../TableHeaderCell';
import SpecialContextContainer from './SpecialContextRenderer';
import { createProps } from './tableCommon';

export default ({
    alwaysUseDivTags,
    canReorderColumns,
    rtProps,
    setSortedColumns,
    tableProps
}) => {
    const {
        headerCellProps,
        headerRowProps,
        layout,
        resizableColumns,
        sortIndicatorOnHover,
        sortable,
        width
    } = tableProps;

    const { headerGroups } = rtProps;

    return (
        <TableHeader alwaysUseDivTags={alwaysUseDivTags} width={width}>
            {headerGroups?.map((headerGroup, headerGroupIndex) => {
                const customHeaderRowProps = createProps(headerRowProps, null, [
                    (headerGroup, headerGroupIndex)
                ]);

                return (
                    <SpecialContextContainer
                        key={headerGroup.id || headerGroupIndex}
                        columnSorting={{
                            columnReordering: canReorderColumns,
                            headerGroup,
                            onColumnSort: setSortedColumns
                        }}
                    >
                        <TableHeaderRow
                            {...headerGroup.getHeaderGroupProps({
                                ...customHeaderRowProps,
                                alwaysUseDivTags,
                                headerGroup
                            })}
                        >
                            {headerGroup.headers.map((column, columnIndex) => {
                                const customHeaderCellProps = createProps(
                                    headerCellProps,
                                    column.headerCellProps,
                                    [column]
                                );

                                return (
                                    <TableHeaderCell
                                        sortIndicatorOnHover={
                                            sortIndicatorOnHover
                                        }
                                        layout={layout}
                                        {...column.getHeaderProps({
                                            ...customHeaderCellProps,
                                            ...(sortable
                                                ? column?.getSortByToggleProps?.()
                                                : {}),
                                            // don't mess with `id` Column reordering needs it
                                            alwaysUseDivTags,
                                            column,
                                            columnIndex,
                                            columnReordering: canReorderColumns,
                                            id: column.id,
                                            resizableColumns,
                                            sortable
                                        })}
                                    />
                                );
                            })}
                        </TableHeaderRow>
                    </SpecialContextContainer>
                );
            })}
        </TableHeader>
    );
};
