/* eslint-disable react/prop-types */
import React, { Fragment } from 'react';
import TableRow from '../TableRow';
import TableCell from '../TableCell';
import { createProps } from './tableCommon';

export default ({
    alwaysUseDivTags,
    cellProps,
    expandable,
    expandedRowTemplate,
    initiallyExpanded,
    layout,
    prepareRow,
    rowProps,
    rows,
    visibleColumns
}) => {
    let rowWidth = null;

    return ({ index: rowIndex, style: rowStyle }) => {
        const row = rows[rowIndex];
        const customRowProps = createProps(rowProps, row.original.rowProps, [
            row.original,
            rowIndex
        ]);

        const nestableActual = true;
        prepareRow(row);

        const combinedStyles = { ...customRowProps?.style, ...rowStyle };

        // HACK: flex layout needs explicit row width
        if (layout === 'flex' && !rowWidth)
            rowWidth = rows?.[0]?.cells.reduce(
                (sum, r) => sum + r.column.totalWidth,
                0
            );

        if (layout === 'flex' && rowWidth)
            combinedStyles.width = `${rowWidth}px`;

        const showExpandedContent =
            expandable &&
            expandedRowTemplate &&
            (row.isExpanded || initiallyExpanded);
        const combinedProps = {
            ...row.getRowProps({
                ...customRowProps,
                row,
                alwaysUseDivTags,
                style: combinedStyles
            })
        };
        // console.log('row props', combinedProps);
        return (
            <Fragment key={`lmnt-row-${combinedProps.key}`}>
                <TableRow {...combinedProps}>
                    {row.cells.map((cell, columnIndex) => {
                        const customCellProps = createProps(
                            cellProps,
                            cell.column.cellProps,
                            [cell]
                        );
                        return (
                            <TableCell
                                layout={layout}
                                columnIndex={columnIndex}
                                {...cell.getCellProps({
                                    ...customCellProps,
                                    cell,
                                    alwaysUseDivTags
                                })}
                            />
                        );
                    })}
                </TableRow>
                {showExpandedContent && (
                    <TableRow
                        key={`${combinedProps.key}_expanded`}
                        alwaysUseDivTags={alwaysUseDivTags}
                    >
                        <TableCell
                            nestable={nestableActual}
                            colSpan={visibleColumns.length}
                            alwaysUseDivTags={alwaysUseDivTags}
                        >
                            {expandedRowTemplate({ row })}
                        </TableCell>
                    </TableRow>
                )}
            </Fragment>
        );
    };
};
