/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TableHeaderDefaultProps,
    TableHeaderPropTypes
} from './generated/propTypes';

const TableHeader = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        children,
        className,
        style,
        width,
        ...customProps
    } = props;
    const styles = { ...style };
    if (width) {
        styles.width = width;
    }

    const Tag = alwaysUseDivTags ? 'div' : 'thead';
    return (
        <Tag
            ref={externalRef}
            className={clsx('lmnt', 'lmnt-table__header', className)}
            {...customProps}
            style={style}
        >
            {children}
        </Tag>
    );
});

TableHeader.displayName = 'TableHeader';
TableHeader.propTypes = TableHeaderPropTypes;
TableHeader.defaultProps = TableHeaderDefaultProps;

export default TableHeader;
