import React, { forwardRef } from 'react';
import clsx from 'clsx';
import { TypoDisplay } from '@element/react-typography';
import {
    TableTopBarPropTypes,
    TableTopBarDefaultProps
} from './generated/propTypes';

const TableTopBar = forwardRef((props, externalRef) => {
    const { title, actions, prominent, filterBar, className, ...customProps } =
        props;

    return (
        <header
            ref={externalRef}
            className={clsx(
                'lmnt-table__top-bar',
                prominent && 'lmnt-table__top-bar--prominent',
                className
            )}
            {...customProps}
        >
            <section className='lmnt-table__top-bar--standard-bar'>
                <div className='lmnt-table__top-bar__title'>
                    <TypoDisplay level={6}>{title}</TypoDisplay>
                </div>
                {actions ? (
                    <div className='lmnt-table__top-bar--standard-bar--action-items'>
                        {actions}
                    </div>
                ) : null}
            </section>
            {filterBar && filterBar}
        </header>
    );
});

TableTopBar.displayName = 'TableTopBar';
TableTopBar.propTypes = TableTopBarPropTypes;
TableTopBar.defaultProps = TableTopBarDefaultProps;

export default TableTopBar;
