/* eslint-disable react/prop-types */
/* eslint-disable  react/no-unstable-nested-components */ // TODO: Fix this lint error on line 115
import clsx from 'clsx';
import React, {
    useMemo,
    forwardRef,
    useEffect,
    useState,
    useRef,
    useCallback,
    useContext
} from 'react';
import TableVerticalHandle from './TableVerticalResizeHandle';
import TableSortIndicator from './TableSortIndicator';
import DragDropRenderer from './common/DragDropRenderer';
import {
    getBackgroundColor,
    getColumnAlignment,
    getContextRootProp
} from './common/tableCommon';
import {
    TableHeaderCellDefaultProps,
    TableHeaderCellPropTypes
} from './generated/propTypes';
import TableContext from './common/TableContext';

const TableHeaderCell = forwardRef((props, customUserRef) => {
    const {
        actionsRenderer,
        alwaysUseDivTags,
        backgroundColor,
        children,
        className,
        column,
        columnIndex,
        columnReordering,
        columnWidth, // columnWidth is here to shut up warnings, we need to figure out why it's being passed in
        customRenderer,
        id,
        fixed,
        layout,
        onClick,
        resizableColumns,
        sortDirection, // sortDirection is here to shut up warnings, we need to figure out why it's being passed in
        sortIndicatorOnHover,
        sortable,
        style,
        wrapMode, // wrapMode is here to shut up warnings, we need to figure out why it's being passed in
        ...customProps
    } = props;

    const context = useContext(TableContext);
    const contextState = context?.state;
    const actionsRendererActual = actionsRenderer || column?.actionsRenderer;
    const Tag = alwaysUseDivTags ? 'div' : 'th';
    const [bgColor, setBgColor] = useState(backgroundColor);
    const fixedActual = fixed || column?.fixed;
    const isLeadingFixed = fixedActual === true || fixedActual === 'leading';
    const isTrailingFixed = fixedActual === 'trailing';
    const fixedColumnsBorder = getContextRootProp(
        context,
        'fixedColumnsBorder',
        true
    );
    const virtualized = getContextRootProp(context, 'virtualized', false);

    const {
        setNodeRef: columnSortingRef,
        dragStyle,
        isDragging,
        isSorting,
        boxSizing,
        ...columnReorderProps
    } = columnReordering ? DragDropRenderer({ id, column }) : {};
    const ref = useRef(columnSortingRef || customUserRef);

    const [fixedOffset, setFixedOffset] = useState(
        isLeadingFixed && layout !== 'standard' && column
            ? column.totalLeft
            : null
    );
    const [fixedWidth, setFixedWidth] = useState();

    useEffect(() => {
        if (!isLeadingFixed) return;

        const th = ref.current;

        if (virtualized) setFixedWidth(th.getBoundingClientRect().width);

        if (layout !== 'standard' && !fixed) return;

        let sibling = th.previousSibling;
        let totalLeft = 0;

        while (sibling) {
            totalLeft += sibling.getBoundingClientRect().width;
            sibling = sibling.previousSibling;
        }
        // console.log('>>>>', totalLeft);
        setFixedOffset(totalLeft);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isLeadingFixed, layout]);

    useEffect(() => {
        if (
            !contextState ||
            !isLeadingFixed ||
            fixedOffset === null ||
            fixedOffset === undefined
        )
            return;

        const existing = contextState.fixedOffsets[columnIndex];
        if (existing || existing === 0) return;

        contextState.fixedOffsets = curr => {
            const updated = [...curr];
            updated[columnIndex] = fixedOffset;
            return updated;
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [fixedOffset]);

    useEffect(() => {
        if (
            !contextState ||
            !isLeadingFixed ||
            fixedWidth === null ||
            fixedWidth === undefined
        )
            return;
        const existing = contextState.fixedWidths[columnIndex];

        if (existing || existing === 0) return;
        contextState.fixedWidths = curr => {
            const updated = [...curr];
            updated[columnIndex] = fixedWidth;
            return updated;
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [fixedWidth]);

    let styles = {
        ...style
    };

    if (fixedWidth) {
        styles.width = `${fixedWidth}px`;
        styles.minWidth = `${fixedWidth}px`;
    }
    if (fixedOffset || fixedOffset === 0) styles.left = `${fixedOffset}px`;
    if (bgColor) styles.backgroundColor = bgColor;
    if (dragStyle) styles = { ...styles, ...dragStyle };

    if (onClick || column?.onClick) styles.cursor = 'pointer';
    if (bgColor) styles.backgroundColor = bgColor;

    const finalProps = {
        ...columnReorderProps,
        ...customProps,
        style: styles
    };

    useEffect(() => {
        if (backgroundColor || !column) return;
        const bg = getBackgroundColor(column);
        if (bg) {
            setBgColor(bg);
        }
    }, [backgroundColor, column]);

    useEffect(() => {
        // Manually pass reference into the columnSortingRef
        columnSortingRef && columnSortingRef(ref.current);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const Content = useMemo(
        () => () => {
            const content = column ? (
                <>
                    {column.header
                        ? column.render('header')
                        : column.render('Header')}
                    {children}
                </>
            ) : (
                children
            );
            return (customRenderer && customRenderer(content)) || content;
        },
        [column, customRenderer, children]
    );

    const Actions = useMemo(
        () => () => {
            const actions = (
                <>
                    {(sortable || column?.sortable) && (
                        <TableSortIndicator
                            sortIndicatorOnHover={sortIndicatorOnHover}
                            column={column}
                        />
                    )}
                    {resizableColumns && (
                        <TableVerticalHandle column={column} />
                    )}
                </>
            );
            return (
                (actionsRendererActual &&
                    actionsRendererActual(actions, columnIndex)) ||
                actions
            );
        },
        [
            actionsRendererActual,
            column,
            columnIndex,
            resizableColumns,
            sortIndicatorOnHover,
            sortable
        ]
    );

    const handleColumnClick = useCallback(
        event => {
            onClick?.(event);
            column?.onClick?.(event, column);
        },
        [column, onClick]
    );

    return (
        <Tag
            className={clsx(
                'lmnt',
                'mdc-data-table__header-cell',
                'lmnt-table__header-cell',
                isTrailingFixed && 'lmnt-table__trailing-fixed-cell',
                isLeadingFixed && 'lmnt-table__leading-fixed-cell',
                fixedColumnsBorder &&
                    'lmnt-table__leading-fixed-cell--trailing-border',
                isDragging && 'lmnt-table__header-cell--dragging',
                isSorting && 'lmnt-table__header-cell--sorting',
                className
            )}
            ref={ref}
            id={id || column?.id}
            role='columnheader'
            scope='col'
            onClick={handleColumnClick}
            {...finalProps}
        >
            <div
                className='lmnt-table__cell-content'
                style={getColumnAlignment(column)}
            >
                <Content />
                <Actions />
            </div>
        </Tag>
    );
});

TableHeaderCell.displayName = 'TableHeaderCell';
TableHeaderCell.propTypes = TableHeaderCellPropTypes;
TableHeaderCell.defaultProps = TableHeaderCellDefaultProps;

export default TableHeaderCell;
