import React, { forwardRef } from 'react';
import clsx from 'clsx';
import {
    TableNoContentPropTypes,
    TableNoContentDefaultProps
} from './generated/propTypes';

const TableNoContent = forwardRef((props, externalRef) => {
    const { children, className, includeTableMarkup, ...customProps } = props;

    const content = (
        <div
            ref={externalRef}
            className={clsx('lmnt-table__no-content', className)}
            {...customProps}
        >
            {children}
        </div>
    );

    return includeTableMarkup ? (
        <tr>
            <td>{content}</td>
        </tr>
    ) : (
        content
    );
});

TableNoContent.displayName = 'TableNoContent';
TableNoContent.propTypes = TableNoContentPropTypes;
TableNoContent.defaultProps = TableNoContentDefaultProps;

export default TableNoContent;
