/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { forwardRef } from 'react';
import { TableRowDefaultProps, TableRowPropTypes } from './generated/propTypes';

const TableRow = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        children,
        className,
        headerGroup, // headerGroup is here to shut up warnings, we need to figure out why it's being passed in
        row,
        rowStyle,
        ...customProps
    } = props;

    const Tag = alwaysUseDivTags ? 'div' : 'tr';
    return (
        <Tag
            ref={externalRef}
            className={clsx(
                'lmnt',
                'mdc-data-table__row',
                'lmnt-table__row',
                className
            )}
            role='row'
            style={rowStyle}
            {...customProps}
        >
            {children}
        </Tag>
    );
});

TableRow.displayName = 'TableRow';
TableRow.propTypes = TableRowPropTypes;
TableRow.defaultProps = TableRowDefaultProps;

export default TableRow;
