/* eslint-disable react/prop-types */
import React, { forwardRef, useState } from 'react';
import { edsRandomId } from '@element/react-common';
import clsx from 'clsx';
import {
    TableSortIndicatorDefaultProps,
    TableSortIndicatorPropTypes
} from './generated/propTypes';

const TableSortIndicator = forwardRef((props, externalRef) => {
    const { column, id, sortIndicatorOnHover } = props;
    const [idActual] = useState(() => id || edsRandomId());

    if (!column) return null;

    const { isSorted, isSortedDesc } = column;
    if (!isSorted) return null;
    return (
        <>
            <button
                ref={externalRef}
                type='button'
                className={clsx(
                    'lmnt',
                    'mdc-icon-button material-icons mdc-data-table__sort-icon-button',
                    isSorted &&
                        !sortIndicatorOnHover &&
                        'lmnt-table__sort-icon-button--visible'
                )}

                // aria-label='Sort by dessert'
                // aria-describedby='dessert-status-label'
            >
                {isSortedDesc ? 'arrow_downward' : 'arrow_upward'}
            </button>
            <div
                className='mdc-data-table__sort-status-label'
                aria-hidden='true'
                id={idActual}
            />
        </>
    );
});

TableSortIndicator.displayName = 'TableSortIndicator';
TableSortIndicator.propTypes = TableSortIndicatorPropTypes;
TableSortIndicator.defaultProps = TableSortIndicatorDefaultProps;

export default TableSortIndicator;
