import React, { forwardRef, useState } from 'react';
import { HoistingContext, hoistingEnabled } from '@element/react-common';
import TableBase from './TableBase';
import TableNoContent from './TableNoContent';
import { TableDefaultProps, TablePropTypes } from './generated/propTypes';
import TableBuilder from './TableBuilder';
import TableContext from './common/TableContext';

// TODO
// - alwaysUseDivs breaks fixed headers
// - fixed columns still broken in virtualized (they are fixed, but horizontal scrolling does not work)
// - height/width does not apply to the correct element when using standard layout

const Table = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        ariaLabel,
        canUnSort,
        children,
        className,
        columnReordering,
        columnStriped,
        columns,
        containerProps,
        data,
        dense,
        disableMultiSort,
        expandable,
        expandedRowTemplate,
        fixedHeader,
        footerContent,
        fullWidth,
        headerContent,
        headerDividerTall,
        headerDividerThemeColor,
        headerThemeColor,
        height,
        hiddenColumns,
        initiallyExpanded,
        layout,
        leadingContent,
        nested,
        noContentIncludeTable,
        noContentMessage,
        noHover,
        onStateChange,
        onWidthChange,
        paginated,
        resizableColumns,
        rowHeight,
        rowStriped,
        sortIndicatorOnHover,
        sortable,
        style,
        topBar,
        trailingContent,
        variableHeight,
        virtualized,
        width,
        ...customProps
    } = props;

    const fixedOffsetsState = useState([]);
    const fixedWidthsState = useState([]);

    const hasData = data?.length > 0 && columns?.length > 0;

    if (!hasData && !children && !noContentIncludeTable)
        return (
            <TableNoContent ref={externalRef}>
                {noContentMessage}
            </TableNoContent>
        );

    const commonProps = {
        ariaLabel,
        className,
        columnStriped,
        containerProps,
        dense,
        fixedHeader,
        fullWidth,
        headerDividerTall,
        headerDividerThemeColor,
        headerThemeColor,
        height,
        leadingContent: topBar || leadingContent,
        nested,
        noContentMessage,
        noHover,
        rowStriped,
        style,
        virtualized,
        width
    };

    const contextState = {
        get fixedOffsets() {
            return fixedOffsetsState[0];
        },
        set fixedOffsets(val) {
            fixedOffsetsState[1](val);
        },
        get fixedWidths() {
            return fixedWidthsState[0];
        },
        set fixedWidths(val) {
            fixedWidthsState[1](val);
        }
    };

    if (!data && children)
        return (
            <TableContext.Provider
                // eslint-disable-next-line react/jsx-no-constructed-context-values
                value={{
                    customProps,
                    rootProps: props,
                    state: contextState
                }}
            >
                <HoistingContext.Provider value={hoistingEnabled}>
                    <TableBase
                        layout='standard'
                        ref={externalRef}
                        trailingContent={trailingContent}
                        {...commonProps}
                        {...customProps}
                    >
                        {children}
                    </TableBase>
                </HoistingContext.Provider>
            </TableContext.Provider>
        );

    return (
        <TableContext.Provider
            // eslint-disable-next-line react/jsx-no-constructed-context-values
            value={{
                customProps,
                rootProps: props,
                state: contextState
            }}
        >
            <TableBuilder ref={externalRef} {...props} />
        </TableContext.Provider>
    );
});

Table.displayName = 'Table';
Table.propTypes = TablePropTypes;
Table.defaultProps = TableDefaultProps;

export default Table;
