/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, {
    useRef,
    useState,
    useContext,
    forwardRef,
    useImperativeHandle
} from 'react';
import { edsRandomId, ConditionalWrapper } from '@element/react-common';
import {
    TableCellDefaultProps,
    TableCellPropTypes
} from './generated/propTypes';
import {
    getBackgroundColor,
    getColumnAlignment,
    getContextRootProp
} from './common/tableCommon';
import CellRenderer from './common/CellRenderer';
import TableContext from './common/TableContext';

const TableCell = forwardRef((props, externalRef) => {
    const {
        alwaysUseDivTags,
        backgroundColor,
        cell,
        children,
        className,
        colSpan,
        columnIndex,
        columnWidth, // columnWidth is here to shut up warnings, we need to figure out why it's being passed in
        customRenderer,
        fixed,
        id,
        layout,
        nestable,
        rowSpan,
        style,
        ...customProps
    } = props;

    const ref = useRef();
    const [idActual] = useState(id || cell?.id || edsRandomId());

    const column = cell?.column || {};

    const Tag = alwaysUseDivTags ? 'div' : 'td';
    const fixedActual = fixed || column.fixed;
    const isLeadingFixed = fixedActual === true || fixedActual === 'leading';
    const isTrailingFixed = fixedActual === 'trailing';
    const bgColor = backgroundColor || getBackgroundColor(column);
    const context = useContext(TableContext);

    const fixedWidth =
        isLeadingFixed && context
            ? context.state.fixedWidths[columnIndex]
            : null;
    const fixedOffset =
        isLeadingFixed && context
            ? context.state.fixedOffsets[columnIndex]
            : null;

    const fixedColumnsBorder = getContextRootProp(
        context,
        'fixedColumnsBorder',
        true
    );

    const styles = { ...style };
    if (fixedWidth) {
        styles.width = `${fixedWidth}px`;
        styles.minWidth = `${fixedWidth}px`;
    }
    if (fixedOffset || fixedOffset === 0) styles.left = `${fixedOffset}px`;
    if (bgColor) styles.backgroundColor = bgColor;

    const cellProps = {
        className: clsx(
            'lmnt',
            'mdc-data-table__cell',
            isTrailingFixed && 'lmnt-table__trailing-fixed-cell',
            isLeadingFixed && 'lmnt-table__leading-fixed-cell',
            fixedColumnsBorder &&
                'lmnt-table__leading-fixed-cell--trailing-border',
            nestable && 'lmnt-table__cell--nestable',
            className
        ),
        ref,
        id: idActual,
        role: 'gridcell',
        style: styles,
        colSpan,
        rowSpan,
        ...customProps
    };

    useImperativeHandle(externalRef, () => ref.current, []);

    return (
        <Tag {...cellProps}>
            <div
                className='lmnt-table__cell-content'
                style={{
                    ...getColumnAlignment(column)
                }}
            >
                <ConditionalWrapper
                    condition={customRenderer}
                    wrapper={customRenderer}
                >
                    <CellRenderer cell={cell}>{children}</CellRenderer>
                </ConditionalWrapper>
            </div>
        </Tag>
    );
});

TableCell.displayName = 'TableCell';
TableCell.propTypes = TableCellPropTypes;
TableCell.defaultProps = TableCellDefaultProps;

export default TableCell;
