/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TableFooterDefaultProps,
    TableFooterPropTypes
} from './generated/propTypes';

const TableFooter = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        alwaysUseDivTags,
        width,
        style,
        ...customProps
    } = props;
    const styles = { ...style };
    if (width) {
        styles.width = width;
    }

    const Tag = alwaysUseDivTags ? 'div' : 'tfoot';
    return (
        <Tag
            ref={externalRef}
            className={clsx('lmnt', 'lmnt-table__footer', className)}
            {...customProps}
            style={style}
        >
            {children}
        </Tag>
    );
});

TableFooter.displayName = 'TableFooter';
TableFooter.propTypes = TableFooterPropTypes;
TableFooter.defaultProps = TableFooterDefaultProps;

export default TableFooter;
