/* eslint-disable react/prop-types */
import React from 'react';
import { Pagination } from '@element/react-pagination';

export default ({ tableProps, rtProps }) => {
    const { paginated, disabled } = tableProps;
    const {
        state,
        pageCount,
        rows,
        pageSize = 0,
        gotoPage,
        setPageSize
    } = rtProps;

    const currentPage = (state?.pageIndex || 0) + 1;
    const totalItems = rows?.length || 0;

    const handleOnPageChange = newPage => {
        gotoPage(newPage - 1);
    };

    const handleOnItemsPerPageChange = newPageSize => {
        setPageSize(newPageSize);
        gotoPage(0);
    };

    return (
        paginated && (
            <Pagination
                controlled
                disabled={disabled}
                totalPages={pageCount}
                currentPage={currentPage}
                totalItems={totalItems}
                onPageChange={handleOnPageChange}
                itemsPerPage={pageSize}
                onItemsPerPageChange={handleOnItemsPerPageChange}
            />
        )
    );
};
