/*
## Table Bugs

- Fixed columns in virtualized mode
- Padding issue with fixed columns and fixed headers
- Table background/borders doesn’t go all the way across (flex layout only)
- Table height/width does not get output in standard layout
*/

/* eslint-disable react/prop-types */
import React, {
    forwardRef,
    useEffect,
    useImperativeHandle,
    useState,
    useMemo,
    useRef
} from 'react';
import { edsWarning } from '@element/react-common';
import {
    formatColumns,
    getCombinedProps,
    getInitialState
} from './common/tableCommon';
import ReactTableManager from './common/ReactTableManager';
import TableBase from './TableBase';
import TableBodyRenderer from './common/TableBodyRenderer';
import TableHeaderRenderer from './common/TableHeaderRenderer';
import PaginationRenderer from './common/PaginationRenderer';

// Maximum curry.
const handleCellUpdate =
    onCellUpdateLocal => (accessor, row, setEditedValue) => e => {
        setEditedValue?.(e.target.value);
        onCellUpdateLocal?.(e.target.value, accessor, row.id, row.original, e);
    };

const TableBuilder = forwardRef((props, externalRef) => {
    const {
        // Note: keeping unused props here to keep react from throwing "unused
        // props" warning
        alwaysUseDivTags,
        ariaLabel,
        canUnSort,
        cellProps,
        children,
        className,
        columnReordering,
        columnStriped,
        columns = [], // protects against null columns
        containerProps,
        data,
        dense,
        disableMultiSort,
        disabled,
        expandable,
        expandedRowTemplate,
        fixedHeader,
        footerContent,
        fullWidth,
        headerCellProps,
        headerContent,
        headerDividerTall,
        headerDividerThemeColor,
        headerRowProps,
        headerThemeColor,
        height,
        hiddenColumns,
        initiallyExpanded,
        layout,
        leadingContent,
        nested,
        noContentIncludeTable,
        noContentMessage,
        noHover,
        onCellUpdate,
        onStateChange,
        onWidthChange,
        paginated,
        resizableColumns,
        rowHeight,
        rowProps,
        rowStriped,
        sortIndicatorOnHover,
        sortable,
        style,
        topBar,
        trailingContent,
        variableHeight,
        virtualized,
        width,
        ...customProps
    } = props;

    const tableRef = useRef();

    const hasFixedColumns = useMemo(
        () => columns.some(x => x.fixed),
        [columns]
    );
    const formattedColumns = useMemo(
        () => formatColumns(columns, handleCellUpdate(onCellUpdate)),
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [columns, onCellUpdate]
    );

    const [sortedColumns, setSortedColumns] = useState(formattedColumns);

    const canReorderColumns = columnReordering && !hasFixedColumns;
    const initialState = getInitialState(columns);
    const hasData = data?.length > 0 && sortedColumns?.length > 0;

    const rtProps = ReactTableManager({
        canUnSort,
        columnReordering: canReorderColumns,
        data,
        disableMultiSort,
        expandable,
        initialState,
        layout,
        paginated,
        resizableColumns,
        sortable,
        columns: sortedColumns,
        virtualized
    });

    const {
        clearSortBy,
        getTableProps,
        page,
        resetResizing,
        rows,
        setHiddenColumns,
        state: rtState,
        toggleAllRowsExpanded
    } = rtProps;

    const alwaysUseDivTagsActual =
        (alwaysUseDivTags && layout !== 'standard') ||
        virtualized ||
        layout === 'absolute';

    // TODO: is there a cleaner way to do this?
    const [expanded, setExpanded] = useState(false);
    useEffect(() => {
        if (!expandable || !initiallyExpanded || expanded) return;
        setExpanded(true);
        toggleAllRowsExpanded();
    }, [expandable, expanded, initiallyExpanded, toggleAllRowsExpanded]);

    useImperativeHandle(
        externalRef,
        () => ({ resetResizing, clearSortBy, table: tableRef.current }),
        [resetResizing, clearSortBy]
    );

    useEffect(() => {
        if (formattedColumns) setSortedColumns(formattedColumns);
    }, [formattedColumns]);

    useEffect(() => {
        if (virtualized && !height)
            edsWarning(
                'Element Table: Height must be defined when using `virtualized`. A default height of 500 has been set.'
            );
    }, [virtualized, height]);

    useEffect(() => {
        if (!hiddenColumns) return;
        setHiddenColumns(hiddenColumns);
    }, [hiddenColumns, setHiddenColumns]);

    const rowsActual = paginated ? page : rows;

    useImperativeHandle(
        externalRef,
        () => ({ resetResizing, clearSortBy, table: tableRef.current }),
        [resetResizing, clearSortBy]
    );

    useEffect(() => {
        onStateChange && onStateChange(rtState);
    }, [onStateChange, rtState]);

    useEffect(() => {
        // link header and body scrolling together
        const body = tableRef.current.querySelector('.lmnt-table__body');
        const header = tableRef.current.querySelector('.lmnt-table__header');

        // HACK: set extra padding for the width of the scrollbar in the header
        header.style.marginRight = `${body.offsetWidth - body.clientWidth}px`;

        const handleScroll = () => {
            header.scrollLeft = body.scrollLeft;
        };

        body.addEventListener('scroll', handleScroll);

        () => {
            body.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const tableProps = getCombinedProps(
        props,
        customProps,
        getTableProps,
        rtState
    );

    return (
        <TableBase
            {...tableProps}
            ref={tableRef}
            noInit
            alwaysUseDivTags={alwaysUseDivTagsActual}
            fixedColumns={hasFixedColumns}
            trailingContent={
                <>
                    <PaginationRenderer tableProps={props} rtProps={rtProps} />
                    {trailingContent}
                </>
            }
        >
            {headerContent}
            <TableHeaderRenderer
                tableProps={props}
                alwaysUseDivTags={alwaysUseDivTagsActual}
                rtProps={rtProps}
                canReorderColumns={canReorderColumns}
                setSortedColumns={setSortedColumns}
            />
            <TableBodyRenderer
                alwaysUseDivTags={alwaysUseDivTagsActual}
                rowHeight={rowHeight}
                variableHeight={variableHeight}
                tableProps={props}
                rtProps={rtProps}
                hasData={hasData}
                rows={rowsActual}
            />
            {footerContent}
        </TableBase>
    );
});

TableBuilder.displayName = 'TableBuilder';

export default TableBuilder;
